.ms-section {
    position: relative;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
.ms-section.ms-table{
    display: table;
    width: 100%;
}
.ms-section.ms-image-left,
.ms-section.ms-image-right {
    width: calc(100% + 1px);
}

.ms-section .ms-image-bg {
    height: 100%;
    background-size: cover;
    display: table;
    width: 100%;
}

.ms-section.ms-image-left .ms-image-bg {
    background-position: 100%;
}

.ms-section.ms-image-right .ms-image-bg {
    background-position: 0;
}

.ms-section.ms-image-left .ms-image-text-wrapper {
    text-align: right;
}

.ms-section.ms-device-left {
    text-align: right;
    padding-right: 15px;
}
.ms-section.ms-device-right {
    text-align: left;
    padding-left: 15px;
}

div.ms-section[class*='ms-device-'] img {
    max-height: 500px;
    max-width: 100%;
}
.ms-section.ms-device-text-left {
    text-align: right;
    float: right;
    padding: 0 15px;
    max-width: 600px;
}
.ms-section.ms-device-text-left hr { margin-right: 0; }
.ms-section.ms-device-text-right hr { margin-left: 0; }
.ms-section.ms-device-text-right {
    text-align: left;
    padding: 0 15px;
    max-width: 600px;
}

.ms-device-text-wrapper {
    font-size: 26px;
    font-weight: 400;
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
}

.ms-image-text-wrapper {
    vertical-align: middle;
    display: table-cell;
}

.ms-image-text-wrapper > span {
    background: rgba(255,255,255,.7);
    display: inline-block;
    width: 100%;
    font-family: 'Oswald', sans-serif;
    font-size: 40px;
    text-transform: uppercase;
    padding: 15px 0;
}


.ms-device-text-wrapper hr {
    margin-bottom: 30px;
    max-width: 300px;
    background: #aaa;
    border-width: 2px;
}

.ms-device-text-wrapper iframe {
    max-width: 100%;
}


.ms-tableCell {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    height: 100%;
}
.ms-easing {
    -webkit-transition: all 0.7s ease-out;
    -moz-transition: all 0.7s ease-out;
    -o-transition: all 0.7s ease-out;
    transition: all 0.7s ease-out;
}
#multiscroll-nav {
    position: fixed;
    z-index: 100;
    margin-top: -32px;
    top: 50%;
    opacity: 1;
}
#multiscroll-nav.right {
    right: 17px;
}
#multiscroll-nav.left {
    left: 17px;
}
#multiscroll-nav ul{
  margin: 0;
  padding: 0;
}
#multiscroll-nav li{
    display: block;
    width: 14px;
    height: 13px;
    margin: 7px;
    position:relative;
}
#multiscroll-nav li a{
    display: block;
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    cursor: pointer;
    text-decoration: none;
}
#multiscroll-nav li .active span{
    background: #333;
}
#multiscroll-nav span{
    top: 2px;
    left: 2px;
    width: 8px;
    height: 8px;
    border: 1px solid #000;
    background: rgba(0, 0, 0, 0);
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    position: absolute;
    z-index: 1;
}
.multiscroll-tooltip {
    position: absolute;
    color: #fff;
    font-size: 14px;
    font-family: arial, helvetica, sans-serif;
    top: -2px;
    white-space: nowrap;
    max-width: 220px;
}
.multiscroll-tooltip.right {
    right: 20px;
}
.multiscroll-tooltip.left {
    left: 20px;
}

@media screen and (max-width: 767px) {
    .ms-device-text-wrapper {
        font-size: 20px;
    }
}

@media screen and (max-width: 479px) {
    .ms-device-text-wrapper {
        font-size: 17px;
    }
}


