// bootstrap variables:
$font-size-base: 14px !default;
$font-size-large: ceil(($font-size-base * 1.25)) !default;
$font-size-small: ceil(($font-size-base * 0.85)) !default;
$line-height-base: 1.428571429 !default;
$line-height-computed: floor(($font-size-base * $line-height-base)) !default;
$padding-small-vertical: 5px !default;
$padding-base-vertical: 6px !default;
$padding-large-vertical: 10px !default;
$padding-small-horizontal:  10px !default;
$padding-base-horizontal: 12px !default;
$padding-large-horizontal: 16px !default;
$bs-height-base: ($line-height-computed + $padding-base-vertical * 2);
$bs-height-lg:   (floor($font-size-large * $line-height-base) + $padding-large-vertical * 2);
$bs-height-sm:   (floor($font-size-small * 1.5) + $padding-small-vertical * 2);
$bs-height-xs:   (floor($font-size-small * 1.2) + $padding-small-vertical + 1);
//
@mixin button-variant($color, $background, $border) {
  color: $color;
  background-color: $background;
  border-color: $border;

  &:hover,
  &:focus,
  &.focus,
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    color: $color;
    background-color: darken($background, 10%);
        border-color: darken($border, 12%);
  }
  &:active,
  &.active,
  .open > &.dropdown-toggle {
    background-image: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      background-color: $background;
          border-color: $border;
    }
  }

  .badge {
    color: $background;
    background-color: $color;
  }
}




.btn-social {
  position: relative;
  padding-left: ($bs-height-base + $padding-base-horizontal);
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  > :first-child {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: $bs-height-base;
    // line-height: ($bs-height-base + 2);
    // font-size: 1.6em;
    text-align: center;
    border-right: 1px solid rgba(0, 0, 0, 0.2);
  }
  &.btn-lg {
    padding-left: ($bs-height-lg + $padding-large-horizontal);
    > :first-child {
      line-height: $bs-height-lg;
      width: $bs-height-lg;
      font-size: 1.8em;
    }
  }
  &.btn-sm {
    padding-left: ($bs-height-sm + $padding-small-horizontal);
    > :first-child {
      line-height: $bs-height-sm;
      width: $bs-height-sm;
      font-size: 1.4em;
    }
  }
  &.btn-xs {
    padding-left: ($bs-height-xs + $padding-small-horizontal);
    > :first-child {
      line-height: $bs-height-xs;
      width: $bs-height-xs;
      font-size: 1.2em;
    }
  }
}

.btn-social-icon {
  @extend .btn-social;
  height: ($bs-height-base + 2);
  width: ($bs-height-base + 2);
  padding: 0;
  > :first-child {
    border: none;
    text-align: center;
    width: 100%!important;
  }
  &.btn-lg {
    height: $bs-height-lg;
    width: $bs-height-lg;
    padding-left: 0;
    padding-right: 0;
  }
  &.btn-sm {
    height: ($bs-height-sm + 2);
    width: ($bs-height-sm + 2);
    padding-left: 0;
    padding-right: 0;
  }
  &.btn-xs {
    height: ($bs-height-xs + 2);
    width: ($bs-height-xs + 2);
    padding-left: 0;
    padding-right: 0;
  }
}

@mixin btn-social($color-bg, $color: #fff) {
  background-color: $color-bg;
  // @113 start
  div[class*="social-anim-2"] & i:nth-child(2) { background-color: $color-bg; }

  div[class*="social-anim-3"] & i:first-child:before { display: inline-block; width: 100%; height: 100%; transition: 0.25s all; }

  div[class*="social-anim-3a"] &:hover          i:first-child:before { box-shadow: inset 0 0 0 1px    $color-bg; }
  div[class*="social-anim-3a"].social-tiny    & i:first-child:before { box-shadow: inset 0 0 0 25px   $color-bg; }
  div[class*="social-anim-3a"].social-small   & i:first-child:before { box-shadow: inset 0 0 0 37.5px $color-bg; }
  div[class*="social-anim-3a"].social-smedium & i:first-child:before { box-shadow: inset 0 0 0 50px   $color-bg; }
  div[class*="social-anim-3a"]                & i:first-child:before { box-shadow: inset 0 0 0 62.5px $color-bg; }
  div[class*="social-anim-3a"].social-lmedium & i:first-child:before { box-shadow: inset 0 0 0 75px   $color-bg; }
  div[class*="social-anim-3a"].social-large   & i:first-child:before { box-shadow: inset 0 0 0 87.5px $color-bg; }
  div[class*="social-anim-3a"].social-huge    & i:first-child:before { box-shadow: inset 0 0 0 100px  $color-bg; }

  div[class*="social-anim-3b"]                &       i:first-child:before { box-shadow: inset 0 0 0 1px    $color-bg; }
  div[class*="social-anim-3b"].social-tiny    &:hover i:first-child:before { box-shadow: inset 0 0 0 25px   $color-bg; }
  div[class*="social-anim-3b"].social-small   &:hover i:first-child:before { box-shadow: inset 0 0 0 37.5px $color-bg; }
  div[class*="social-anim-3b"].social-smedium &:hover i:first-child:before { box-shadow: inset 0 0 0 50px   $color-bg; }
  div[class*="social-anim-3b"]                &:hover i:first-child:before { box-shadow: inset 0 0 0 62.5px $color-bg; }
  div[class*="social-anim-3b"].social-lmedium &:hover i:first-child:before { box-shadow: inset 0 0 0 75px   $color-bg; }
  div[class*="social-anim-3b"].social-large   &:hover i:first-child:before { box-shadow: inset 0 0 0 87.5px $color-bg; }
  div[class*="social-anim-3b"].social-huge    &:hover i:first-child:before { box-shadow: inset 0 0 0 100px  $color-bg; }

  div[class*="social-anim-4"] & i:first-child:after { background: darken($color-bg, 20%); }

  div[class*="social-anim-5"] & i:first-child:before { background: $color-bg }
  div[class*="social-anim-5"] & i:nth-child(2):before { background: darken($color-bg, 20%); }

  div[class*="social-inverse-2"] & i:before { color: $color-bg }
  div[class*="social-color-native"] & i:before { color: $color-bg }
  div[class*="social-inverse-1"] &:hover i:before { color: $color-bg }
  div[class*="social-hover-color-native"] &:hover i:before { color: $color-bg }

  div[class*="social-coloured"] &:hover i:before { color: darken($color-bg, 20%); }
  // @113 end

  @include button-variant($color, $color-bg, rgba(0,0,0,.2));
}


