*, *:before, *:after { box-sizing: inherit; }

html {
  width: 100%;
  font-size: 15px;
  box-sizing: border-box;
  &.container { padding: 0; }
}

body {
  max-width: 100%;
  margin: 0 auto !important;
  font-size: 15px;
  font-family: $lato;
  color: #444;
  line-height: 1.5;
  overflow-x: hidden;
  &.container {
    .animsition         { width: inherit; }
    .navbar-affix.affix { width: inherit; }
    .parallax-mirror    { width: inherit !important; left: inherit !important; }
  }
  &.rtl { direction: rtl; }
}

.animsition-loading       { max-width: 100%;
  &:after                 { max-width: 100%; }}
.animsition-overlay-slide { max-width: 100%; }


a { color: #333; transition: all .25s; }
a:hover, a:focus { color: #000; text-decoration: none; }
a:focus { outline: none; }
a.link {
  border-bottom: 1px solid #ccc;
  &:hover { border-color: #676767; }
}

.button {
  transition: 0.25s all;
  font-family: $montserrat;
  font-size: 14px;
  text-shadow: 1px 1px 1px rgba(0,0,0,.1);
  letter-spacing: .5px;
  padding: 0 30px;
  position: relative;
  font-weight: 500;
  &:disabled, .disabled, .is-disabled {
    border: 0 !important;
    cursor: not-allowed !important;
  }
}

.button-rounded {
  border-radius: 2px;
}

.btn { outline: none !important; font-size: 15px;}
.btn-file { vertical-align: bottom; }

.button-dropdown-list > li > a {
  font-family: $montserrat;
  font-weight: 500;
}

.button-tiny  {
  font-size: 10px; padding: 0 10px;
  &.button-shrink { padding: 0 7px; }
}
.button-small {
  font-size: 12px; padding: 0 20px;
  &.button-shrink { padding: 0 13px; }
}
.button-large {
  font-size: 20px; padding: 0 40px;
  &.button-shrink { padding: 0 27px; }
}
.button-jumbo {
  font-size: 24px; padding: 0 50px;
  &.button-shrink { padding: 0 34px; }
}
.button-giant {
  font-size: 28px; padding: 0 60px;
  &.button-shrink { padding: 0 40px; }
}

.button-shrink { padding: 0 20px; }

.button:not(.button-theme):not(.button-primary):not(.button-action):not(.button-highlight):not(.button-royal):not(.button-caution):not(.button-plain):not(.button-inverse) {
  color: #666;
}

.button.disabled, .button.is-disabled, .button:disabled {
  border: 0 !important;
}

.button-border-white {
  transition: .25s background;
  color: #eee !important;
  span { transition: 0.25s right, 0.25s color !important; }
  &:hover {
    background: #fff;
    border-color: #fff;
    color: #222 !important;
    span { color: #222; }
  }
}

.button.disabled,
.button.is-disabled,
.button:disabled {
  opacity: 1 !important;
}

textarea:focus { outline:0 !important; }
select,
.popover { box-shadow: none; }

pre[class*="language-"] { margin: 0 !important; }
.pswp { display: none; }
.pswp--open { display: block !important; }

pre {
  margin-top: 0;
  margin-bottom: 1rem;
}

input {
  box-shadow: none;
  &:focus { outline: none; }
}

input[type="file"]:focus,
input[type="radio"]:focus,
input[type="checkbox"]:focus {
  outline: none;
}

p.content,
i.content {
  line-height: 1.75;
}

ul[class^='list'], ul[class*=' list'],
ol[class^='list'], ol[class*=' list'],
.dl-horizontal dd, .dl-horizontal dt {
  line-height: 1.75;
}

.form-control-label { padding-top: 7px; padding-bottom: 7px; margin-bottom: 0; }

.input-sm, .input-group-sm > .form-control, .input-group-sm > .input-group-btn > .btn {
  height: 32px !important;
  font-size: 12px !important;
  padding: 5px 16px !important;
}

.input-lg, .input-group-lg > .form-control, .input-group-lg > .input-group-addon, .input-group-lg > .input-group-btn > .btn {
  height: 47px !important;
  font-size: 18px !important;
}

.input-group-addon {
  border-radius: 2px;
}

.tt-menu {
  border-radius: 2px;
}

.fileinput-new.input-group .btn-file, .fileinput-new .input-group .btn-file {
  border: 0;
  transition: background .25s, color .25s;
  border-radius: 0 2px 2px 0;
}

.fileinput {
  line-height: 40px;
  &.input-group .form-control {
    line-height: 26px;
  }
  .fileinput-filename {
    margin-left: 5px;
  }
}

.form-control {
  box-shadow: none;
  border-radius: 2px;
  border: 1px solid #ccc;
  height: 40px;
  line-height: 40px;
  font-size: 15px;
  padding: 8px 16px;
  &:focus { box-shadow: none; }
}

textarea.form-control {
  line-height: 1.5;
}

.input-group-btn button {
  height: 40px;
}

@media screen and (min-width: $screen-sm) {
  .form-inline .form-group {
    vertical-align: top;
  }
}

.form-group textarea {
  display: block;
}


.dropcap:first-letter {
  float: left;
  font-size: 3rem;
  line-height: 0.9;
  margin-right: .5rem;
}

.panel {
  margin-bottom: 2px;
  box-shadow: none;
  border-radius: 3px;
}

.accordion-no-border .panel { border-color: transparent !important; }
.accordion-vertical { box-shadow: 0 0 0 1px #ddd !important; }
.well { box-shadow: none; }

.panel-theme .panel-heading {
  color: #fff;
}

.panel-heading {
  border-radius: 0;
  border-bottom: 0;
  position: relative;
  letter-spacing: .5px;
}

.accordion .panel-heading:after {
  font-family: 'fontawesome';
  content: '\f105';
  position: absolute;
  right: 20px;
  font-size: 20px;
  opacity: .75;
  top: 20px;
  line-height: 0;
}

.panel.acc-open .panel-heading {
  border-bottom-left-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
  &:after {
    transform: rotate(90deg);
  }
}

.panel .panel-heading:after {
  transition: transform 0.25s;
  transform: rotate(0deg);
}

.panel-title {
  position: relative;
  line-height: 20px;
  font-size: 16px;
}

.accordion-combined {
  .panel {
    margin-bottom: 0;
    border-radius: 0;
    &:first-child { border-top-left-radius: 4px; border-top-right-radius: 4px; border-bottom: 0; }
    &:last-child { border-bottom-left-radius: 4px; border-bottom-right-radius: 4px; border-top: 0; }
  }
}

.accordion-no-color {
  .panel {
    border-color: #ddd;
  }
  .panel-heading {
    background: transparent !important;
    color: #444;
  }
}

.accordion-border .panel-heading { border-bottom: 1px solid #ddd; }

.av-inner { padding: 15px; }
.av-inner-media {
  padding: 0;
  height: 100%;
  video { height: 100%; }
  .av-image {
    height: 100%;
    background-size: cover;
    background-position-y: bottom;
  }
}

.liteAccordion.rounded .slide > h2 span {
  letter-spacing: 1px;
}

.liteAccordion.dark .av-inner {
  color: #bbb;
  a { color: #fff; }
}

.liteAccordion.light .slide > h2 {
  background: #f5f5f5;
  text-shadow: none;
  span {
    box-shadow: none;
    background: #ddd;
    color: #333;
    font-weight: 600;
  }
}

.liteAccordion.light .slide > h2.selected span,
.liteAccordion.light .slide h2.selected span:hover,
.liteAccordion.light .slide > h2.selected b {
  background-color: #ccc;
}


.liteAccordion .slide > h2 { z-index: 15; }
.liteAccordion.dark .slide > h2 { text-shadow: none; }

.liteAccordion.light .slide > div { background-color: #f5f5f5; }
.liteAccordion.light .slide > h2 { background-color: #f5f5f5; }
.liteAccordion.light { background-color: #f5f5f5; }
.liteAccordion.dark .slide > div { background-color: #111; }
.liteAccordion.dark .slide > h2 { background-color: #111; }
.liteAccordion.dark { background-color: #111; }

.list-circle { list-style-type: circle; }
.list-square { list-style-type: square; }
.list-group-item-heading { font-weight: 500; }

.has-error {
  .form-control {
    box-shadow: none;
    border-color: $red_form !important;
    &:focus {
      box-shadow: none;
      border-color: lighten($red_form, 15%) !important;
    }
  }
  .input-group-addon {
    color: $red_form;
    border-color: $red_form !important;
    background-color: lighten($red_form, 50%);
  }
}

.has-success {
  .form-control {
    box-shadow: none;
    border-color: $green_form !important;
    &:focus {
      box-shadow: none;
      border-color: lighten($green_form, 15%) !important;
    }
  }
  .input-group-addon {
    color: $green_form;
    border-color: $green_form !important;
    background-color: lighten($green_form, 50%);
  }
}

.has-warning {
  .form-control {
    box-shadow: none;
    border-color: $orange_form !important;
    &:focus {
      box-shadow: none;
      border-color: lighten($orange_form, 15%) !important;
    }
  }
  .input-group-addon {
    color: $orange_form;
    border-color: $orange_form !important;
    background-color: lighten($orange_form, 50%);
  }
}

h1, h2, h3, h4, h5, h6,
.h1,.h2,.h3,.h4,.h5,.h6 {
  font-family: $raleway;
  margin-top: 0;
  margin-bottom: .5rem;
  font-weight: 600;
  &.slab {
    font-weight: 500;
  }
}

.slab-headings {
  h1, h2, h3, h4, h5, h6,
  .h1,.h2,.h3,.h4,.h5,.h6 {
    font-family: $oswald;
    font-weight: 500;
  }
}

h1 small, h1 .small, .h1 small, .h1 .small,
h2 small, h2 .small, .h2 small, .h2 .small,
h3 small, h3 .small, .h3 small, .h3 .small,
h4 small, h4 .small, .h4 small, .h4 .small,
h5 small, h5 .small, .h5 small, .h5 .small,
h6 small, h6 .small, .h6 small, .h6 .small {
  font-weight: 500;
}

hr {
  color: #aaa;
  border-top: 1px solid #eee;
}

hr.fa-divider {
  display: block;
  margin: 35px auto;
}

i.fa-divider-icon {
  position: relative;
  top: -52px;
  left: 9px;
  border-radius: 50%;
  display: inline-block;
  width: 36px;
  height: 36px;
  line-height: 34px;
  text-align: center;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  color: #aaa;
}

hr.fa-divider-center + i {
  left: 0;
}
hr.fa-divider-right + i {
  left: -9px;
}
hr.fa-divider-rounded + i { border-radius: 4px; }
hr.fa-divider-square + i { border-radius: 0; }
hr.fa-divider-half {
  width: 500px;
  max-width: 100%;
  margin-left: 0;
}
hr.fa-divider-quarter {
  width: 250px;
  max-width: 100%;
  margin-left: 0;
}
hr.fa-divider-half,
hr.fa-divider-quarter {
  &.center {
    margin-left: auto;
    + i {
      left: 0;
    }
  }
  &.right {
    margin-left: auto;
    margin-right: 0;
    + i {
      left: -9px;
    }
  }
}
hr.fa-divider-big-icon + i {
  font-size: 1.75rem;
  width: 3.5rem;
  top: -60px;
  height: 3.5rem;
  line-height: 3.5rem;
}

hr.narrow {
  margin-top: 4px;
  margin-bottom: 6px;
}

.text-divider {
  position: relative;
  > span {
    position: absolute;
    top: -20px;
    background: #fff;
    padding: 0 25px 0 0;
  }
  &.text-center {
    > span {
      background: none !important;
      padding: 0;
      left: 0;
      right: 0;
      > span {
        background: #fff;
        padding: 0 25px;
      }
    }
  }
  &.text-right > span {
    right: 0;
    padding: 0 0 0 25px;
  }
}



// ###################
// ### breadcrumbs ###
// ###################

%breadcrumb-arrow-before {
  content: "";
  position: absolute;
  border-width: 20px 0 20px 16px;
  border-style: solid;
  border-color: #eee #eee #eee transparent;
  left: -16px;
}

%breadcrumb-arrow-after {
  content: "";
  position: absolute;
  border-top: 20px solid transparent;
  border-bottom: 20px solid transparent;
  border-left: 16px solid #eee;
  right: -16px;
}

.breadcrumb-arrow,
.breadcrumb-arrow-solid {
  > li {
    position: relative;
    background: #eee;
    line-height: 40px;
    transition: all .25s;
    &:before { content: "" !important; }
    &.active {
      background: #ddd;
      &:before { border-color: #ddd #ddd #ddd transparent; }
      &:after { border-left: 16px solid #ddd; }
      &:hover, &:focus { background: #ddd; }
    }
    > a {
      padding-right: 8px;
      background: #eee;
      height: 40px;
      display: inline-block;
      &:before {
        transition: all .25s;
        @extend %breadcrumb-arrow-before;
      }
      &:after {
        transition: all .25s;
        @extend %breadcrumb-arrow-after;
      }
    }
    &:first-child {
      > a {
        padding: 0 10px 0 17px;
        &:before {
          left: 0;
        }
      }
    }
    &:last-child {
      padding: 0 8px 0 10px;
      &:before { @extend %breadcrumb-arrow-before; }
      &:after  { @extend %breadcrumb-arrow-after;  }
    }
    &:hover, &:focus {
      background: #ccc;
      > a {
        background: #ccc;
        color: #000;
        &:before { border-color: #ccc #ccc #ccc transparent; }
        &:after { border-color: transparent transparent transparent #ccc; }
      }
    }
  }
}

.breadcrumb-arrow       > li { margin: 0 8px; }
.breadcrumb-arrow-solid > li { margin: 0 6px; }

.breadcrumb-arrow-small {
  > li {
    margin: 0 5px;
    line-height: 30px;
    &:before { border-width: 15px 0 15px 10px !important; }
    &:after {
      border-top: 15px solid transparent !important;
      border-bottom: 15px solid transparent !important;
      border-left-width: 10px !important;
      right: -10px !important;
    }
    > a {
      height: 30px;
      &:before { border-width: 15px 0 15px 10px !important; left: -10px; }
      &:after {
        border-top: 15px solid transparent;
        border-bottom: 15px solid transparent;
        border-left: 10px solid #eee;
        right: -10px;
      }
    }
    &:last-child:before { left: -10px; }
  }
  &.breadcrumb-arrow-solid > li { margin: 0 3px; }
}

.breadcrumb-arrow-rounded {
  li:first-child,
  li:first-child a,
  li:first-child a:before {
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
  }
}

.breadcrumb-arrow-rounded li:last-child,
.breadcrumb-arrow-rounded li:last-child a {
  padding: 0 16px 0 10px;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  &:after { border: none; }
}




.breadcrumb {
  padding: 1rem 0;
  border-radius: 2px;
  background-color: transparent;
  margin-top: 20px;
  font-size: 13px;
  a {
    color: #777;
    &:hover {
      color: #000;
    }
  }
  > li + li:before {
    color: #777 !important;
  }
  &.text-right {
    padding-left: 0;
    padding-right: 15px;
  }
  .active { color: #000; }
}

.breadcrumb-justify {
  text-align: justify;
  &:after {
    width: 100%;
    display: inline-block;
    content: ".";
    visibility: hidden;
  }
}

.breadcrumb-raquo > li + li:before {
  content: "»";
  font-size: 16px;
  padding: 0 10px 0 6px;
}

.breadcrumb-pill > li {
  padding-right: 5px;
  &:before {
    content: "" !important;
    padding: 0 !important;
  }
  a {
    padding: 7px 15px;
    border-radius: 200px;
    background: #eee;
    color: #444;
  }
  &.active {
    padding: 5px 15px;
    border-radius: 200px;
    background: #ddd;
  }
  &:hover a {
    background: #ccc;
    color: #000;
  }
}

.breadcrumb-arrow,
.breadcrumb-arrow-solid,
.breadcrumb-pill {
  background-color: transparent !important;
}

// ###################
// ### blockquotes ###
// ###################

blockquote {
  border-left: 5px solid rgba(0,0,0,0.1);
  font-size: 15.5px;
  footer, small, .small {
    line-height: 1.5;
    color: #888;
  }
}
.blockquote-filled {
  border-radius: 2px;
  border-left: 0 !important;
  padding: 25px 35px;
  p { color: #333; }
  small {
    color: #333;
    font-weight: 600;
  }
}
.blockquote-filled-bw {
  border-radius: 2px;
  border-left: 0 !important;
  background-color: #444;
  color: #eee;
  padding: 25px 35px;
  small { color: #fff; }
}
.blockquote-marks {
  border-left: 0 !important;
  &:before {
    content: '“';
    font-size: 7rem;
    position: absolute;
    color: rgba(0,0,0,.1);
    top: -40px;
    left: 0;
  }
  &:after {
    content: '”';
    font-size: 7rem;
    position: absolute;
    color: rgba(0,0,0,.1);
    left: 160px;
    top: 65px;
  }
}


// ###################
// ### full screen ###
// ###################

.full-screen {
  height: 100vh;
  overflow: hidden;
  position: relative;

  background-size: cover !important;
  background-position: center !important;
  top: 0;
  > .container {
    top: 50%;
    position: relative;
    text-align: right;
    color: #ddd;
    transform: translateY(-50%);
  }
  &.full-screen-less {
    height: 75vh;
  }
}

.full-screen-text {
  top: 50%;
  transform: translateY(-50%);
  position: relative;
  padding: 20px 0 20px;
  &.third { top: 33vh; }
  &.two-thirds { top: 66vh; }
  &.bottom {
    top: auto;
    bottom: 0;
    transform: none;
    position: absolute;
    width: 100%;
  }
}

.full-screen-title-wrapper {
  text-align: center;
  color: #eee;
  padding: 0 50px;
  &.inverse {
    color: #111;
    &.solid-color {
      color: #111 !important;
    }
  }
  &.solid-color {
    color: #eee !important;
  }
}

.full-screen-shadow {
  top: -20px;
  text-align: center;
  user-select: none;
  cursor: default;
  z-index: -200;
  font-size: 50px;
  // big text-shadow blur will cause performance issues!
  text-shadow: 0 0 15px #000;
  color: transparent;
  position: absolute;
  width: 100%;
}
.full-screen-title {
  font-family: $montserrat;
  display: inline-block;
  font-weight: 500;
}
.full-screen-subtitle {
  font-weight: 400;
  font-size: 22px;
  font-family: $raleway;
  margin-bottom: 0;
  line-height: 1.7;
  display: inline-block;
}

.full-screen-container {
  padding: 50px 0;
  h3 {
    font-family: $montserrat;
    line-height: 1.5;
    margin-bottom: 0;
    font-weight: 500;
  }
}

.full-screen-links {
  float: right;
  margin-right: 100px;
  ul {
    line-height: 2.4;
  }
  a {
    color: #222 !important;
    &:hover {
      color: #111 !important;
    }
  }
}

.page-title {
  padding-top: 50px;
  padding-bottom: 15px;
  border-bottom: 1px dashed #ddd;
  position: relative;
  h1 {
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 1px;
    margin-bottom: 1rem;
  }
  span {
    text-align: center;
    display: block;
    font-weight: 500;
    font-family: $raleway;
  }
  .breadcrumb { margin-top: 5px; }
  > .container {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
  }

  div.pull-left * {
    text-align: left;
  }
  div.pull-right * {
    text-align: right;
  }
  div.pull-left, div.pull-right {
    margin-bottom: 35px;
    *:first-child {
      margin-bottom: 7px;
    }
  }
}

.img-iconbox-fixed {
  .img-wrapper {
    max-height: 225px;
    overflow: hidden;
  }
}

.pt-clear-bg { height: 100px; }

@media screen and (max-width: $screen-sm-max) {
  .page-title {
    .pull-left, .pull-right {
      float: none !important;
      text-align: center;
      * { text-align: center !important; }
    }
  }
}

.pt-gradient {
  border: 0 !important;
  padding: 0;
  > .container {
    position: static;
    height: inherit;
    vertical-align: bottom;
    width: 100vw;
    display: table-cell;
    background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,0) 25%,rgba(255,255,255,1) 100%);
  }
}

.pt-medium {
  height: 300px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.pt-tall {
  height: 400px;
  background-size: cover;
  background-position: center;
  position: relative;
}
.video-pt-bg {
  overflow: hidden;
  video {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
  }
}

.pt-narrow {
  padding: 15px;
  div.pull-left,
  div.pull-right {
    margin-bottom: 0;
  }
  .breadcrumb {
    margin-bottom: 0;
    margin-top: 10px;
  }
}


// ################
// ### feedback ###
// ################

.feedback-inline {
  .feedback-text{
    display: inline-block;
    vertical-align: middle;
    .author {
      margin-bottom: 0.5rem;
    }
  }
  .circle-img { display: inline-block; }
}

.circle-img {
  height: 10rem;
  display: inline-block;
  img {
    max-width: 100%;
    height: 100%;
    margin: 0 auto;
  }
}

.feedback-title { margin-bottom: 2rem; }
.feedback blockquote { border-left: 0 !important; line-height: 1.75; }

.feedback-bg {
  padding-top: 30px;
  padding-bottom: 30px;
  background-size: cover;
  background-position: center;
}

.feedback-grid {
  padding-left: 10px;
  padding-right: 10px;
  .circle-img {
    height: 5em;
    width: 5em;
    float: left;
    margin: 10px;
  }
  .feedback-text {
    blockquote {
      font-size: 1rem;
      margin-bottom: 5px;
    }
    .author {
      margin-bottom: 15px;
    }
  }
}

.feedback > .feedback-text,
.feedback-carousel .feedback-text {
  max-width: 767px;
  margin: auto auto 15px auto;
}

// #################
// ### copyright ###
// #################

.copyright { background: #fff; position: relative; }

.copyright-text {
  margin-bottom: 0;
  font-family: $lato;
  font-size: 15px;
}

.copyright-social {
  a { margin: 2px; }
}

@media screen and (max-width: $screen-xs-max) {
  .copyright-text { font-size: 16px; }
  .full-screen-container h3 {
    font-size: 18px;
  }
}

.copyright-nav {
  margin-bottom: 0;
  padding-left: 0;
  li {
    list-style-type: none;
    display: inline-block;
    margin-right: 13px;
    &:after {
      content: '/';
      position: relative;
      left: 8px;
    }
    &:last-child:after {
      content: '';
    }
  }
}

.copyright-nav-dashes li:after {
  content: '-';
}


// ##################
// ### pagescroll ###
// ##################

.section-slider {
  height: 100vh;
  overflow: hidden;
  z-index: 3;
  width: 100%;
}

// ##############
// ### mosaic ###
// ##############

.mosaic-image,
.mosaic-image-2,
.mosaic-image-3 {
  overflow: hidden;
  height: 100%;
  width: 100%;
  background-size: cover !important;
  img { width: 100%; }
}

.mosaic .row {
  margin: 0;
  & > div {
    padding: 0;
    overflow: hidden;
  }
}

.mosaic-text {
  padding: 40px;
  display: table-cell;
  vertical-align: middle;
}


@media screen and (min-width: $screen-md) and (max-width: $screen-md-max) {
  .mosaic-text {
    padding: 25px;
  }
}

.mosaic-wrapper {
  display: table;
  width: 100%;
  height: 100%;
}

.mosaic-inner {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  i {
    font-size: 3em;
    transition: color 0.25s;
    &:hover {
      cursor:pointer;
    }
  }
  a { cursor: default; }

}

.mosaic-inner-video {
  display: table-cell;
  iframe {
    width: 100%;
    height: 100%;
  }
  video {
    bottom: 0;
    position: absolute;
    min-height: 100%;
  }
}


// ##############
// ### device ###
// ##############

.device-image img { width: 220px; }


.device-left-side {
  .device-feature p { padding-right: 60px; }
  .device-icon { float: right; }
}

.device-right-side {
  .device-feature p { padding-left: 60px; }
  .device-icon { float: left; }
}

.device-long-title {
  .device-feature {
    margin-bottom: 50px;
    h4 {
      letter-spacing: .5px;
    }
  }
  .device-icon {
    transition: color 0.25s;
    i {
      font-size: 3rem;
      margin-top: 8px;
    }
  }
}

@media screen and (max-width: $screen-xs-max) {
  .device-long-title {
    .device-icon i { margin-top: 0; }
    .device-feature { margin-bottom: 3rem; }
  }
}

.device-small-image {
  position: absolute;
  width: 27% !important;
  bottom: 0;
  right: 0;
}

.device-slider {
  width: 220px;
  display: inline-block !important;
  .slick-track {
    padding-top: 0 !important;
    img {
      width: 220px !important;
    }
  }
}

.big-device-subject img { width: 100%; }
.big-device-subject-wrapper { z-index: 1; }

.big-device-text {
  line-height: 1.75;
}

.perspective-device-image img {
  width: 100%;
}

.perspective-device-text {
  font-size: 18px;
  font-family: $raleway;
  line-height: 1.75;
}

@media screen and (max-width: $screen-xs-max) {
  .perspective-device-image img {
    max-height: 400px;
    width: auto;
    margin: auto;
    display: block;
    max-width: 100%;
  }
  .perspective-device-text {
    font-size: 15px;
    font-weight: 400;
  }
}

// ############
// ### logo ###
// ############

.logo-wrapper {
  line-height: 58px;
  font-size: 50px;
  height: 60px;
  text-transform: none !important;
}

.logo-section {
  font-size: 20px;
  line-height: 0;
  display: block;
  font-family: $raleway;
  font-weight: 500;
}

.logo-section-small { font-size: 14px; }

.menu-navbar-collapse .text-logo { transform: scale(0.75); }

.text-logo {
  color: inherit !important;
  display: inline-block;
  transition: all linear 0.5s;
  text-align: center;
  &:hover,
  &:active,
  &:focus,
  &:visited {
    color: inherit !important;
  }
}

@media screen and (min-width: $screen-md) {
  .text-logo {
    position: absolute; // for smooth scaling
  }
  .navbar-items-left .text-logo { position: relative; }
  .navbar-items-left .text-logo { position: relative; }
}

.block-logo img {
  max-height: 250px;
  margin: 0 auto;
}

// ############
// ### edge ###
// ############

$edge-height: 42px;
.edge {
  height: $edge-height;
  .social-icons {
    position: relative;
    bottom: 6px;
    font-size: 0;
    margin: 0;
    a {
      height: $edge-height;
      &:hover i:before { color: #fff; }
      &:last-child { border-right: 1px solid #E7E7E7; }
    }
  }
}
.edge-social .social-icons a:last-child { border-right: 0 !important; }
.edge-left-block {
  @extend .edge;
  line-height: $edge-height;
  a {
    padding: 0 10px;
    white-space:nowrap;
  }
}
.edge-right-block {
  @extend .edge;
  line-height: $edge-height;
}

.footer ~ .edge .edge-left-block { margin-bottom: 0;}

.edge-button { margin: 0 5px; font-size: 16px; }

.edge-links {
  .edge-left-block a,
  .edge-right-block a {
    padding: 0 10px;
    border-right: 1px solid #E7E7E7;
    margin: 0;
  }
  .edge-left-block a {
    float: left;
    &:last-child { border-right: 0 !important; }
  }
  .edge-right-block a {
    float: right;
    &:first-child { border-right: 0 !important; }
  }
}

@media screen and (max-width: $screen-sm-max) {
  .edge { height: $edge-height*2; }
  .edge-left-block { text-align: center; height: $edge-height }
  .edge-right-block { text-align: center; height: $edge-height }
  .edge-links .edge-left-block a, .edge-links .edge-right-block a { float: none; }
  .edge-links .edge-right-block a {
    &:first-child { border-right: 1px solid #E7E7E7 !important; }
    &:last-child { border-right: 0 !important; }
  }
}

@media screen and (max-width: 450px) {
  .edge-left-block {
    font-size: 15px;
    padding: 0;
    a { padding: 0; }
  }
  .edge-right-block {
    > a:nth-of-type(1) {
      top: 0;
      position: absolute;
      line-height: initial;
    }
    > a:nth-of-type(2) {
      top: 12px;
      position: relative;
      line-height: initial;
    }
  }
}


// ############
// ### tabs ###
// ############

.dropdown-menu {
  font-size: 15px;
}

.nav-pills > li > a,
.dropdown-menu {
  border-radius: 3px;
}

.nav-tabs > li > a {
  border-radius: 3px 3px 0 0;
}

.nav-tabs {
  &.tabs-right li {
    a { border-radius: 0 3px 3px 0; }
    &.active {
      a, a:hover, a:focus {
        border: 1px solid;
        border-color: #ddd #ddd #ddd #fff;
        background-color: transparent;
      }
    }
  }
  &.tabs-left li {
    a { border-radius: 3px 0 0 3px; }
    &.active {
      a, a:hover, a:focus {
        border-radius: 3px 0 0 3px;
        border: 1px solid;
        border-color: #ddd #fff #ddd #ddd;
        background-color: transparent;
      }
    }
  }
  &.nav-border-top li {
    a, a:hover, a:focus {
      border-radius: 0;
    }
    &.active {
      a, a:hover, a:focus {
        border-top-color: transparent;
      }
    }
  }
  &.nav-border-bottom li {
    a, a:hover, a:focus {
      border-radius: 0;
      border-color: transparent;
    }
  }
}

.nav-tabs > li > a:hover { border-color: #ddd #ddd #ddd; }

.side-icons {
  .side-tab {
    border: 0;
    text-align: center;
    li {
      margin-bottom: 0;
      margin-right: 0;
      display: inline-block;
      width: 100%;
      a {
        border: 0 !important;
        border-radius: 0 !important;
        text-align: center;
        padding: 0;
        color: #222;
        &:hover { color: #222; }
        i {
          font-size: 4em;
          padding: 15px;
          transition: 0.25s all;
        }
      }
      &.active a i {
        &:hover,
        &:focus {
          background: transparent;
        }
      }
    }
  }

  .nav-tabs > li.active > a,
  .nav-tabs > li.active > a:hover,
  .nav-tabs > li.active > a:focus {
    background-color: transparent !important;
  }
}


@media screen and (max-width: $screen-sm-max) {
  .side-icons .side-tab li {
    width: auto;
    float: none;
  }
  .side-tabs .side-tab li {
    text-align: center;
  }
}

.tab-content { text-align: justify; }

.side-tabs-styled {
  ul {
    padding-top: 0;
    li {
      margin-bottom: 0 !important;
      transition: all 0.3s;
      a {
        border-radius: 0 !important;
        border-color: transparent !important;
      }
    }
  }
  .tabs-left li {
    margin-right: 0;
  }
  .tabs-right li {
    margin-left: 0;
  }
}

.tab-pane {
  > p:only-child {
    padding: 20px;
    margin-bottom: 0;
  }
  .blog-single-post-comments p {
    padding: 0;
  }
  .team p {
    padding: 0;
    margin: 0 0 10px;
  }
  .user-main-info p {
    padding: 0;
  }
  .widget {
    padding-top: 20px !important;
    padding-bottom: 20px;
  }
}

.nav-tabs > li.active > a,
.nav-tabs > li.active > a:hover,
.nav-tabs > li.active > a:focus {
  color: #000;
}

.tabs-filled {
  &.nav-tabs > li > a {
    margin-right: -1px;
    border-radius: 0;
    background: #eee;
    border: 1px solid #ddd;
  }
  &.nav-tabs > li.active > a,
  &.nav-tabs > li.active > a:hover,
  &.nav-tabs > li.active > a:focus {
    background-color: #fff;
    border-bottom-color: transparent;
  }
}

.tabs-center.nav-tabs {
  text-align: center;
  > li {
    float: none;
    display: inline-block;
    margin-right: -3px;
    a { border-right: 0 !important; }
    &:last-child a { border-right: 1px solid #ddd !important; }
  }
}

.tabs-a-right.nav-tabs {
  text-align: right;
  border-left: none;
  > li {
    float: none;
    display: inline-block;
    margin-right: -4px;
    &:last-child { margin-right: 1px; }
  }
}

.tabs-border {
  border-bottom: 0;
  ~ .tab-content {
    border: 1px solid #ddd;
  }
}

@media screen and (max-width: $screen-xs-max) {
  .tabs-left { border-right: 0 !important; }
  .tabs-right { border-left: 0 !important; }
  .side-tabs .tab-content { margin: 10px auto; }
}


// #####################
// ### image overlay ###
// #####################

.overlay-part {
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,.75);
  display: table;
}

.image-overlay-text {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  color: rgba(255,255,255,.9);
  p { color: rgba(255,255,255,.9); }
}

.image-overlay-title {
  margin-bottom: 1rem;
}

.image-overlay-gradient .overlay-part     { background: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)); }
.image-overlay-gradient-top .overlay-part { background: linear-gradient(to top,    rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)); }

.image-overlay-left {
  .image-overlay-text {
    padding-right: 50%;
    padding-left: 30px;
  }
  &.image-overlay-gradient {
    .overlay-part {
      background: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
      width: 100%;
    }
  }
}

.image-overlay-right {
  .overlay-part { margin-left: 50%; }
  &.image-overlay-gradient {
    .overlay-part {
      margin-left: 0;
      background: linear-gradient(to left, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    }
    .image-overlay-text {
      padding-left: 50%;
      padding-right: 30px;
    }
  }
}

.image-overlay-block  {
  background-size: cover;
  background-position: center !important;
}

@media screen and (max-width: $screen-xs-max) {
  .image-overlay-block {
    height: auto;
    .overlay-part {
      padding: 45px 0;
    }
  }
}


// ################
// ### headings ###
// ################

.fw-heading {
  display: table;
  width: 100%;
  color: #222;
}

.fw-heading-content {
  display: table-cell;
  vertical-align: middle;
}

.fw-heading-bg .fw-heading-text {
  background-color: rgba(255,255,255,.5);
  margin-bottom: 0;
  + p {
    background-color: rgba(255, 255, 255, 0.5);
    padding-bottom: 20px;
  }
}

.fw-heading-text + p {
  font-size: 16px;
}

.fw-heading-text span {
  display: block;
  font-size: 1.5rem;
}

.fw-button-text,
.fw-heading-text {
  padding: 15px;
  font-weight: 300;
  span.strong {
    font-weight: 600;
    border-color: transparent !important;
    transition: none;
  }
}

.fw-button-text {
  &:not(.fw-button-no-anim):hover {
    i { margin-left: 10px; }
    span { border-bottom: 1px solid rgba(0,0,0,.3); }
  }
  span { border-bottom: 1px solid rgba(0,0,0,0); }
  i, span { transition: all .25s; }
}

.fw-heading-bg,
.fw-button-bg,
.social-icons-fw-image,
.block-counter-multiple-bg,
.block-counter-single-img-multiple {
  background-size: cover;
  background-position: center;
}

.fw-grayscale {
  background: #ccc;
}

.heading-sub {
  .heading-main {
    margin-bottom: .5rem;
    border-bottom: 1px solid #ddd;
    display: inline-block;
    padding-bottom: .5rem;
  }
}

.heading-border,
.heading-line,
.heading-sub,
.heading-zig-zag {
  margin-bottom: 2rem;
}

.heading-border .heading-main {
  margin-bottom: .5rem;
  border-bottom: 1px solid #ddd;
  padding-bottom: .5rem;
}

.heading-zig-zag {
  background: url("/img/app/zig-zag.png") 0 45% repeat-x;
  .heading-main {
    display: inline-block;
    background: #fff;
    padding: 0 25px;
  }
}

.heading-line {
  position: relative;
  &:before {
    content: '';
    position: absolute;
    width: 100%;
    border-top: 1px solid #ddd;
    top: 45%;
    right: 0;
    z-index: -2;
  }
  .heading-main {
    display: inline-block;
    background: #fff;
    padding: 0 25px;
  }
}

.fw-button {
  display: table;
  width: 100%;
  a {
    width: 100%;
    min-height: inherit;
    display: table-cell;
    vertical-align: middle;
    color: #111;
  }
}

.fw-button-theme a {
  color: #222;
  transition: none;
  &:hover, &:focus {
    color: #000 !important;
  }
}

.fw-button-theme-alt a {
  &:hover,
  &:focus {
    i { color: #fff; }
    span { border-bottom: 1px solid rgba(255,255,255,.3); color: #fff !important; }
  }
}

.fw-button-theme-inverse a {
  color: #eee;
  &:hover, &:focus {
    i { color: #fff; }
    span { border-bottom: 1px solid rgba(255,255,255,.3); color: #fff !important; }
  }
}

.fw-button-theme-inverse-alt a {
  color: #eee;
  &:hover, &:focus {
    color: #000 !important;
  }
}

.fw-button-gray-to-theme,
.fw-button-gray-to-theme-alt {
  background: #444;
  &:hover {
    span {
      border-bottom: transparent !important;
    }
  }
}

.fw-button-gray-to-theme a {
  color: #eee;
}

.heading-alt {
  margin-bottom: 30px;
  .heading-main {
    text-transform: uppercase;
  }
  p {
    font-size: 17px;
    font-weight: 300;
    font-family: $roboto;
    color: #888;
  }
  &:after {
    width: 50px;
    height: 2px;
    background: #555;
    content: '';
    display: block;
    position: relative;
    top: 2px;
  }
}

.heading-alt-2 {
  margin-bottom: 30px;
  .heading-main {
    font-family: $roboto;
    font-weight: 300;
    margin-bottom: 15px;
  }
  &:after {
    content: '';
    display: block;
    height: 2px;
    background: #555;
    width: 50px;
    top: 15px;
    position: relative;
  }
  &.no-line:after {
    display: none;
  }
}

.heading-alt, .heading-alt-2 {
  &.text-center:after {
    margin: auto;
  }
  &.text-right:after {
    margin-left: auto;
  }
}


// #################
// ### countdown ###
// #################

.countdown-block-img .countdown { background-color: rgba(255,255,255,0.5); }

.countdown-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.countdown-block {
  display: table;
  width: 100%;
}

.countdown-block-img { background-size: cover; }

.countdown-chunk {
  display: inline-block;
  margin: auto 1rem;
  .number {
    display: block;
    font-size: 6rem;
    position: relative;
    &:after {
      content: '';
      border-bottom: 1px solid #888;
      display: block;
      width: 35px;
      margin: auto;
      position: relative;
      bottom: 6px;
    }
  }
  .word {
    font-size: 1.5rem;
  }
}

.countdown-alt { font-size: 3.5rem; }

.countdown-border {
  .countdown-chunk {
    width: 200px;
    height: 200px;
    padding-top: 12px;
    margin: 1rem .5rem auto .5rem;
    border: 1px solid #444;
  }
  &.countdown-small .countdown-chunk {
    width: 150px;
    height: 150px;
    padding-top: 15px;
  }
  &.countdown-tiny .countdown-chunk {
    width: 100px;
    height: 100px;
    padding-top: 17px;
  }
}

.countdown-circle .countdown-chunk { border-radius: 200px; }
.countdown-rounded .countdown-chunk { border-radius: 6px; }

.countdown-small {
  .number {
    font-size: 4rem;
    &:after {
      bottom: 4px;
      width: 30px;
    }
  }
  .word { font-size: 1.25rem; }
  .countdown-alt { font-size: 2.5rem; }
}

.countdown-tiny {
  .number {
    font-size: 2rem;
    &:after {
      bottom: 1px;
      width: 20px;
    }
  }
  .word { font-size: 1rem; }
  .countdown-alt { font-size: 1.5rem; }
}

.countdown-vertical .countdown-chunk {
  margin: auto 3rem;
  .number:after {
    border-bottom: 0;
    border-right: 1px solid #888;
    height: 100%;
    right: -3rem;
    bottom: -24%;
    position: absolute;
  }
  &:last-child .number:after {
    border-right: 0;
  }
}


// ###########
// ### map ###
// ###########

.map-embed {
  overflow: hidden;
  width: 100%;
  height: 375px;
}

.map-embed-canvas {
  width: 100%;
  height: 100%;
  img {
    max-width: none !important;
    background: none !important;
  }
}

.map-prevent-scroll + .map-canvas { box-shadow: 0 4px 10px #000; }

.map-canvas {
  height: 100%;
  min-height: 528px;
  transition: all 0.25s;
}

.map-canvas-wrapper {
  height: 100%;
  min-height: 528px;
  transition: all 0.25s;
  top: 0;
  position: relative;
  &.small {
    min-height: 250px;
    .map-canvas { min-height: 250px; }
  }
}

.map-prevent-scroll {
  width: 100%; // if 30, there is small offset
  height: 100%;
  position: absolute;
  z-index: 19;
  background-color: rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.map-fullscreen { height: 100vh; }

// #################
// ### subscribe ###
// #################

.subscribe form,
.coming-soon form,
.password-recovery form,
._404-input form {
  display: table;
  margin: 0 auto;
}

._404 ._404-input {
  max-width: 560px;
  width: 100%;
}

.password-recovery {
  max-width: 500px;
}

.subscribe .input-group,
.coming-soon .input-group {
  max-width: 400px;
