// ################
// ### progress ###
// ################

.progress {
  box-shadow: none !important;
  border-radius: 3px;
  margin-bottom: 16px;
  span { font-size: 1rem; }
  &.progress-slim,
  &.progress-slim-2 {
    height: 3px;
    overflow: visible;
    .progress-bar {
      overflow: visible;
    }
  }
  &.progress-slim {
    .progress-label {
      position: absolute;
      right: -6px;
      background: #444;
      top: -30px;
      font-family: $montserrat;
      font-weight: 500;
      padding: 0 5px;
      border-radius: 2px;
      color: #fff;
      &:after {
        content: '';
        width: 0;
        height: 0;
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-top: 6px solid #444;
        bottom: -5px;
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
      }
    }
  }
  &.progress-slim-2 {
    .progress-label {
      position: absolute;
      right: 0;
      top: -20px;
      font-weight: 500;
      font-family: $montserrat;
    }
  }
}

.progress-bar {
  box-shadow: none !important;
}

.progress-line-title {
  margin-bottom: 2px;
  font-family: $montserrat;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
}

.progress .progress-bar {
  transition: width 1s ease-in-out;
  border-radius: 0;
  position: relative;
  &:first-child {
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  &:last-child {
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
  }
}

.block-progress.progress-no-anim .progress .progress-bar {
  transition: width 0s;
}

.vertical-progress-wrapper {
  height: 300px;
  .progress {
    width: 40px;
    margin-right: 20px;
    display: inline-block;
    float: none;
  }
  .progress-bar {
    border-bottom-right-radius: 0;
    border-top-left-radius: 3px;
    span {
      margin-top: 3px;
      display: inline-block;
    }
  }
}

