.checkbox label::after,
.radio label::after {
  top: 50% !important;
}

.radio label:after {
  margin-top: -5.5px;
}

.checkbox label::after {
  margin-top: -9.5px;
  left: 0.5px;
}

.checkbox label::after,
.checkbox label::before,
.radio label::before,
.radio label::after {
  margin-left: -17px;
}

.checkbox label,
.radio label {
  padding-left: 8px;
}

