$tiny-height:   20px;
$tiny-width:    40px;
$small-height:  30px;
$small-width:   60px;
$medium-height: 40px;
$medium-width:  80px;
$large-height: 50px;
$large-width:  100px;
$huge-height:   60px;
$huge-width:    120px;

.fancy-switch-round {
  + label {
    padding: 2px;
    width: $medium-width;
    height: $medium-height;
    border-radius: $medium-height;
    background-color: #dddddd;
    &:before, &:after {
      display: block;
      position: absolute;
      top: 1px;
      left: 1px;
      bottom: 1px;
      content: "";
    }
    &:before {
      right: 1px;
      background-color: #f1f1f1;
      border-radius: $medium-height;
      transition: background 0.4s;
    }
    &:after {
      width: $medium-height - 2px;
      background-color: #fff;
      border-radius: 100%;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
      transition: margin 0.4s;
    }
  }
  &:checked + label:after {
    margin-left: $medium-height;
  }

  &.fancy-switch-tiny {
    + label {
      width: $tiny-width;
      height: $tiny-height;
      border-radius: $tiny-height;
      &:before {
        border-radius: $tiny-height;
      }
      &:after {
        width: $tiny-height - 2px;
      }
    }
    &:checked + label:after {
      margin-left: $tiny-height;
    }
  }
  &.fancy-switch-small {
    + label {
      width: $small-width;
      height: $small-height;
      border-radius: $small-height;
      &:before {
        border-radius: $small-height;
      }
      &:after {
        width: $small-height - 2px;
      }
    }
    &:checked + label:after {
      margin-left: $small-height;
    }
  }
  &.fancy-switch-large {
    + label {
      width: $large-width;
      height: $large-height;
      border-radius: $large-height;
      &:before {
        border-radius: $large-height;
      }
      &:after {
        width: $large-height - 2px;
      }
    }
    &:checked + label:after {
      margin-left: $large-height;
    }
  }
  &.fancy-switch-huge {
    + label {
      width: $huge-width;
      height: $huge-height;
      border-radius: $huge-height;
      &:before {
        border-radius: $huge-height;
      }
      &:after {
        width: $huge-height - 2px;
      }
    }
    &:checked + label:after {
      margin-left: $huge-height;
    }
  }
}


.fancy-switch-flat {
  + label {
    padding: 2px;
    width: $medium-width;
    height: $medium-height;
    background-color: #dddddd;
    border-radius: $medium-height;
    transition: background 0.4s;
    &:before, &:after {
      display: block;
      position: absolute;
      content: "";
    }
    &:before {
      top: 2px;
      left: 2px;
      bottom: 2px;
      right: 2px;
      background-color: #fff;
      border-radius: $medium-height;
      transition: background 0.4s;
    }
    &:after {
      top: 4px;
      left: 4px;
      bottom: 4px;
      width: $medium-height - 8px;
      background-color: #dddddd;
      border-radius: $medium-height - 8px;
      transition: margin 0.4s, background 0.4s;
    }
  }
  &:checked + label:after {
    margin-left: $medium-height;
  }

  &.fancy-switch-tiny {
    + label {
      width: $tiny-width;
      height: $tiny-height;
      border-radius: $tiny-height;
      &:before {
        border-radius: $tiny-height;
      }
      &:after {
        width: $tiny-height - 8px;
        border-radius: $tiny-height - 8px;
      }
    }
    &:checked + label:after {
      margin-left: $tiny-height;
    }
  }
  &.fancy-switch-small {
    + label {
      width: $small-width;
      height: $small-height;
      border-radius: $small-height;
      &:before {
        border-radius: $small-height;
      }
      &:after {
        width: $small-height - 8px;
        border-radius: $small-height - 8px;
      }
    }
    &:checked + label:after {
      margin-left: $small-height;
    }
  }
  &.fancy-switch-large {
    + label {
      width: $large-width;
      height: $large-height;
      border-radius: $large-height;
      &:before {
        border-radius: $large-height;
      }
      &:after {
        width: $large-height - 8px;
        border-radius: $large-height - 8px;
      }
    }
    &:checked + label:after {
      margin-left: $large-height;
    }
  }
  &.fancy-switch-huge {
    + label {
      width: $huge-width;
      height: $huge-height;
      border-radius: $huge-height;
      &:before {
        border-radius: $huge-height;
      }
      &:after {
        width: $huge-height - 8px;
        border-radius: $huge-height - 8px;
      }
    }
    &:checked + label:after {
      margin-left: $huge-height;
    }
  }
}

.fancy-switch-rotate {
  + label {
    padding: 2px;
    width: $medium-width;
    height: $medium-height;
    font-weight: 400;
    font-family: $lato;
    &:before, &:after {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      color: #fff;
      font-size: 18px;
      text-align: center;
      line-height: $medium-height;
    }
    &:before {
      background-color: #dddddd;
      content: attr(data-off);
      transition: transform 0.5s;
      backface-visibility: hidden;
    }
    &:after {
      content: attr(data-on);
      transition: transform 0.5s;
      transform: rotateY(180deg);
      backface-visibility: hidden;
    }
  }
  &:checked + label:before {
    transform: rotateY(180deg);
  }
  &:checked + label:after {
    transform: rotateY(0deg);
  }

  &.fancy-switch-tiny + label {
    width: $tiny-width;
    height: $tiny-height;
    &:before, &:after {
      line-height: $tiny-height;
      font-size: 10px;
    }
  }

  &.fancy-switch-small + label {
    width: $small-width;
    height: $small-height;
    &:before, &:after {
      line-height: $small-height;
      font-size: 14px;
    }
  }

  &.fancy-switch-large + label {
    width: $large-width;
    height: $large-height;
    &:before, &:after {
      line-height: $large-height;
      font-size: 22px;
    }
  }

  &.fancy-switch-huge + label {
    width: $huge-width;
    height: $huge-height;
    &:before, &:after {
      line-height: $huge-height;
      font-size: 26px;
    }
  }
}

